body {
  padding: 0;
  font-family: sans-serif;
}

.toolsPane {
	float: right;
	display: inline-block;
}

.appContainer {
	text-align: center;
	width: 100%;
	height: 100%;

}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
input[type=number] {
    -moz-appearance:textfield;
}

input {
  border-radius: 5px;
}

body {
	margin: 0;
}

.comabatOrderPaneContainer {
	display: inline-block;
}
.characterInfoPane {
  float: left;
  display: inline-block;
  width: 20vw;
  border-right: 2px solid black;
  height: calc(100vh - 20px);
  padding: 10px;
  text-align: center;
}


.inputCardContainer {
  border: 1px solid black;
  padding: 3px;
  background-color: #ffffff;
  display: inline-block;
  text-align: left;
}

#modifier {
	width:20px;

}
#name {
	width: 100px;
	margin-bottom: 2px;
}

.characterInfoCard {
	border: 1px solid black;
	margin-bottom: 10px;
	padding: 3px;
	border-radius: 5px;
	display: inline-block;
	text-align: left;
	background-color: #ffffff;
}

.characterInfoCard p {
	margin: 5px 0px;
}
.activePlayer {
	width: 150px;
	border: 1px solid;
	border-radius: 5px;
	height: 100px;
	margin-top: 20px;
	text-align: center;
	margin: auto;
}
.multiRoundEffect {
	width: auto;
	border: 1px solid;
	border-radius: 5px;
	height: auto;
	margin: 20px;
	display: inline-block;
	background-color: #ffffff;
}

.multiRoundEffect p{
	margin: 3px;
}
.combatOrderPane {
	text-align: center;
	display: inline-block;
	padding-top: 40px;
}
.multiRoundEffectCreator {
	border: 1px solid black;
	margin-bottom: 10px;
	padding: 3px;
	border-radius: 5px;
	display: inline-block;
	text-align: left;
	margin: 5px;
	background-color: #ffffff;
}

.multiRoundEffectCreator h4 {
	margin-top: 5px;
	margin-bottom: 10px;
	text-align: center;
}

#effectInfoInputForm {
	text-align: center
}
.toolsPane {
	width: 15vw;
	border-left: 2px solid black;
	height: 100vh ;
}
